import React, { Component } from 'react';

class Player extends Component {
    constructor(props, context) {
        super(props, context);
        console.log(this.props.orientation)
        this.isPortrait = this.props.orientation === 'PORTRAIT';
        this.playEnd = this.playEnd.bind(this);

        var listener = {
            onbufferingstart: function() {
              console.log("Buffering start.");
            },
          
            onbufferingprogress: function(percent) {
              console.log("Buffering progress data : " + percent);
            },
          
            onbufferingcomplete: function() {
              console.log("Buffering complete.");
            },
            onstreamcompleted: () => {
              console.log("Stream Completed");
              window.webapis.avplay.stop();
              this.playEnd();
            },
          
            oncurrentplaytime: function(currentTime) {
              console.log("Current playtime: " + currentTime);
            },
          
            onerror: function(eventType) {
              console.log("event type error : " + eventType);
            },
          
            onevent: function(eventType, eventData) {
              console.log("event type: " + eventType + ", data: " + eventData);
            },
          
            onsubtitlechange: function(duration, text, data3, data4) {
              console.log("subtitleText: " + text);
            },
            ondrmevent: function(drmEvent, drmData) {
              console.log("DRM callback: " + drmEvent + ", data: " + drmData);
            }
          };
          window.webapis.avplay.setListener(listener);
    }

    playEnd() {
        //console.log('call play end success~')
        //console.log(this.props)
        this.props.end();
    }

    componentDidMount() {
        let files = {
            landscape :'file:///opt/usr/home/owner/apps_rw/JZCzPFqYfX/res/wgt/assets/landscape.mp4',
            portrait:'file:///opt/usr/home/owner/apps_rw/JZCzPFqYfX/res/wgt/assets/port.mp4'
        };
        
        window.webapis.avplay.open('https://media.w3.org/2010/05/sintel/trailer.mp4');

        // Case 1: Application resolution 1920x1080 px
        window.webapis.avplay.setDisplayRect(0,0,1920,1080);
        window.webapis.avplay.setDisplayMethod('PLAYER_DISPLAY_MODE_AUTO_ASPECT_RATIO');
        window.webapis.avplay.prepare();

        window.webapis.avplay.play();

        window.webapis.avplay.play();
        
        if (this.isPortrait) window.webapis.avplay.setDisplayRotation('PLAYER_DISPLAY_ROTATION_90');
    }

    render() {
        const elemStyle ={
            left:'0px',
            top:'0px',
            width: this.isPortrait? '1080px':'1920px',
            height:this.isPortrait? '1920px':'1080px'
        }

        return(<object style={elemStyle} type='application/avplayer'></object>)
    }
}

export default Player;