import React, { Component } from 'react';

import HomePage from './page/HomePage';
import DetailPage from './page/DetailPage';
import Player from './page/Player';
import Tracker from './component/Tracker';
import './style/App.css';
import './style/responsive.css';


const keysList=["1","2","3","4","5","6","7","8","9","0","ColorF0Red","ColorF1Green","ColorF2Yellow","ColorF3Blue","MediaPlay","MediaPause","MediaFastForward","MediaRewind","MediaStop" , "ChannelUp",
					"ChannelDown"];


class App extends Component {
    constructor(props, context) {
      super(props, context);
      this.setPortrait = this.setPortrait.bind(this);
      this.setLandscape = this.setLandscape.bind(this);
      this.startPlayer = this.startPlayer.bind(this);
      this.setRoute = this.setRoute.bind(this);
      this.playEnd = this.playEnd.bind(this);

      this.state = {
        perData : {dom:0, load:0},
        route : 'home',
        orientation: 'LANDSCAPE'//window.webapis.displayrotator.getOrientation()
      }
      this.dom = 0;

      //for(let t=0;t<keysList.length;t++)
				//window.tizen.tvinputdevice.registerKey(keysList[t])
    }

    setPortrait() {
      console.log('-------setPortrait------')
      //window.webapis.displayrotator.setOrientation("PORTRAIT");
      //window.webapis.displayrotator.getStatus();
    }
    setLandscape() {
      console.log('-------setLandscape------')
      //window.webapis.displayrotator.setOrientation('LANDSCAPE');
    }

    componentDidMount() {


      window.addEventListener('keydown', e => {
        //console.log(`e.keyCode: ${e.keyCode}`)
        if(e.keyCode === 10009) {
          
          if(this.state.route === 'player') {
            window.webapis.avplay.stop();
            this.setRoute('home');
          }
        }

      });      
    }

    setRoute(path) {
      this.setState({
        route: path
      })
    }
    componentWillUnmount() {
      window.removeEventListener("resize", () => {

      });
    }

    startPlayer(type){
      console.log(`video :${type}`);

      this.setRoute('player');
    }
    playEnd() {
      this.setRoute('home');
    }

    render() {
        
        //console.log(`App: ${this.props.currentFocus}`)

        const renderSwitch = (path) => {
          switch(path) {
            case 'home':
          return(             
                <HomePage orientation={this.state.orientation} startPlayer={this.startPlayer} currentFocus={this.props.currentFocus}/>
              )
            case 'item':
              return(
                <DetailPage />
              )
            case 'player':
              return(
                <Player orientation={this.state.orientation} end={this.playEnd}/>
              )
            default:
                return(<div>Error Path!</div>)
          }
        };

        
        return (<div>
          {renderSwitch(this.state.route)}
          </div>);
    }
}

export default App;
