import React from 'react';
import ReactDOM from 'react-dom';
import './style/index.css';
import { renderOnAppLoaded } from 'react-tv';
import { withNavigation } from 'react-tv-navigation';

import App from './App';

function Demo({currentFocusPath}) {
  //console.log(`currentFocusPath: ${currentFocusPath}`);

  return (<App currentFocus={currentFocusPath}/>)
}

const AppWithNavigation = renderOnAppLoaded(withNavigation(Demo));

ReactDOM.render(
  <AppWithNavigation />,
  document.getElementById('root')
);

