import React, { Component } from 'react';

import {FocusableTitle} from '../component/focusTitle';
import ForYouPic from '../assets/nav_ForYou_focus.png';
import DancePic from '../assets/nav_Dance_focus.png';
import ConcertPic from '../assets/nav_Concert_focus.png';


const Nav = (props) => {
    const { isLandscape, currentFocus, selected} = props;
    
    //console.log(`path: ${ currentFocus}`)

    let bgUrl = ForYouPic;

    if(typeof currentFocus === 'string') {
        if(currentFocus.indexOf('for') >=0) {
            bgUrl = ForYouPic;
        } else if(currentFocus.indexOf('dance') >=0) {
            bgUrl = DancePic;
        } else if(currentFocus.indexOf('concert') >=0) {
            bgUrl = ConcertPic;
        }
    }
    
    const selectedAnimation = {
        animation: 'fadein ease-in',
        animationDuration: '.5s',
    }

    let isTest = false;

    return(
        <div className="nav-area" style={isLandscape ? {background: `url(${bgUrl}) no-repeat center center`} :{}}>
            <div className="nav-fonts title">Tube</div>
            {isLandscape ?  
                <div className="focus-title-area">
                    <FocusableTitle
                        onEnterPress={selected}
                        focusPath={`nav-foryou`} 
                        title="For You" 
                        index="0"/>
                    <FocusableTitle focusPath={`nav-dance`} title="Dance" index="1" onEnterPress={selected}/>
                    <FocusableTitle focusPath={`nav-concert`} title="Concert" index="2" onEnterPress={selected}/>
                </div>
                : ''}
        </div>
    )
}

export default Nav;