import React, { Component } from 'react';
import FocusableImage from './focusImage';

import{foryouList, danceList, concertList} from '../imageLoader';

class ImageList extends Component {
    constructor(props, context) {
        super(props, context);

        //console.log(props)
        this.name = this.props.path;

    }

    render() {
        const {path, mode, selected, currentFocus} = this.props;

        let pos = 0;
        if(typeof currentFocus === 'string' && currentFocus.indexOf(this.name) >=0 ) {
            //console.log(`currentFocus:${currentFocus}, name:${this.name}`)
            pos = currentFocus.split('-')[1]-1;
            //pos = pos >= 3 ? pos :0;
           
        }

        let imgAry = [];
        imgAry = imgAry.concat(foryouList).concat(danceList).concat(concertList);
        
        //console.log(`pos:${pos}`)
        let move = {
            //transform: `translate3d(${(mode === 'LANDSCAPE' ? -130 : -70) * pos}px,0,0)`,
            //transition: `transform 3s linear`,
            //-webkit-transition: 'all 1s linear'
        };
        const createList = (path) => {
            
            const lists = []
            for(let i =0; i<50; i++) {
                imgAry.sort(() => 0.5 - Math.random());
                lists.push(
                    <FocusableImage 
                    key={`${path}-${i}`}
                    focusPath={`${path}-${i}`} 
                    src={imgAry.slice(Math.floor(Math.random() * Math.floor(8)))} 
                    oritation={mode} 
                    onEnterPress={selected} />
                )
            }

            return lists;
        }
        
        return (
            <div className={mode === 'LANDSCAPE' ? "landscape-list-area" : 'item-area'} style={move}>
                {
                    createList(path)
                }
            </div>
        )
    }
}
export default ImageList