import React from 'react';
import { withFocusable } from 'react-tv-navigation';

const TitleElement = ({setFocus, index, title, focused, focusPath, test}) => {
    //if( focusPath === 'title-1') setFocus();
    //console.log(this.props)
    const iconList = ['icon-movie', 'icon-dance', 'icon-concert'];
    const iconFocus = ['icon-movie-focused', 'icon-dance-focused', 'icon-concert-focused'];
    
    const selectedAnimation = {
        animation: 'fadein ease-in',
        animationDuration: '.3s',
    }

    return (
        <div className={`title-pos ${focused?'focus-title-focused':'focus-title'}`} >
            <div id={focusPath} className={focused? iconFocus[index]:iconList[index]}></div>
            <div className="catalog">{title}</div>
        </div>)
}



const FocusableTitle = withFocusable(TitleElement);


export  {FocusableTitle, TitleElement};