import React, { Component } from 'react';
import { withFocusable } from 'react-tv-navigation';
import styled from 'styled-components';
import Slider from "react-slick";
// Import css files
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import playerIcon from '../assets/player_play_focused.webp';
import{foryouList, danceList, concertList} from '../imageLoader';

const ImageContainer = styled.div`
            height: 450px;
            width: ${450/600*400}px;
            border-radius: 8px;            
            margin-bottom: 45px;
            margin-right: ${props => props.landscpe ? '35px':'26px'};
            /*background : url(${props => props.src}) no-repeat;
            background-size:100% 100%;*/
            box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 2px 0px;

            &:focus {
                transform: scale(1.1);
                transition: all .2s ease-in;
                box-shadow: 20px 0 20px 0 black;
            }
        `;

const ImageDesArea = styled.div`
    position: relative;
    top:-150px;
    color: white;
    height:150px;
    width:100%;
    background-color: rgba(0, 0, 0, .7);
    z-index:9;
`
const DesTitle = styled.div`
color: white;
font-size:24px;
margin-bottom: 25px;
text-shadow: 0px 4px 4px rgba(255, 255, 255, 0.5); 
text-align: center;
`
const DesInfo = styled.div`
color: white;
font-size:18px;
text-shadow: 0px 2px 2px rgba(255, 255, 255, 0.5); 
text-indent: 1em;
`
const PlayIconOverlay = styled.div`
    position: relative;
    top:50%;
    transform: translate(100%, -50%);
    background-image: url(${props => props.src});
    height: 100px;
    width: 100px;
    opacity:.8;
    z-index:9;
`
const ImageBg =styled.div`
    height:450px;
    border-radius: 8px;
    width:100%;
    background : url(${props => props.src}) no-repeat;
    background-size:100% 100%;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 2px 0px;
    
`

class FocusImage extends Component {
    constructor(props, context) {
        super(props, context);
        //console.log(focused)
        this.focused = props.focused;
        this.src = props.src;
        this.oritation = props.oritation;
        this.focusPath = props.focusPath;
        
        this.onTransitionEnd = this.onTransitionEnd.bind(this)
        this.onFocus = this.onFocus.bind(this);
        this.onBlur = this.onBlur.bind(this);
    }

    onFocus(e) {
        console.log(`onFocus: ${this.focusPath}`)
    }

    onBlur(e) {
        
        console.log(`onBlur: ${this.focusPath}`)
    }

    onTransitionEnd(e) {
        //console.log(`src:${this.src}, this.focused: ${this.focused}`)
    }

    render() {
        var settings = {
            dots: false,
            infinite: true,
            autoplay: true,
            speed: 430*(Math.floor(Math.random() * Math.floor(6))+1),
            slidesToShow: 1,
            rows: 1,
            arrows:false
          };
        //console.log(playerIcon)

        const pos = {
            position: 'relative',
            top: '0px'
        }
        //<PlayIconOverlay src={playerIcon} />
        //console.log(`${this.props.focusPath}`)
        return(
            <ImageContainer
                onClick={() => this.props.onEnterPress()}
                landscpe={this.oritation ==='LANDSCAPE' ? true : false}>                    
                    <Slider {...settings} style={pos}>                        
                        {this.src.map((img, i) => {
                            return(
                                <div key={`${this.props.focusPath}-${i}`}>
                                    <ImageBg 
                                    src={img} 
                                    />
                                </div>
                            )
                        })}
                    </Slider>
                    <ImageDesArea>
                        <DesTitle>{`Title : ${this.props.focusPath}`}</DesTitle>
                        <DesInfo>A military contractor reconnects with an old flame in Hawaii, then falls for a hard-nosed pilot.</DesInfo>
                    </ImageDesArea>
            </ImageContainer>
        )
    }
}

const FocusableImage = withFocusable(FocusImage);


export default FocusableImage;

